body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

table, tr, th, td {
  border: 1px solid black; }

.scss-homepage-container {
  width: 1200px;
  margin: 20px auto; }

.scss-product-container {
  display: flex;
  flex-direction: row;
  border-bottom: 4px solid #dfdfdf;
  padding-bottom: 20px; }

.scss-grid-md-3 {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 23%;
  margin: 0px 15px; }

.scss-grid-md-6 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  margin: 0px 15px; }

.scss-shopping-cart {
  margin: auto;
  width: 100%; }

.scss-shopping-cart-title {
  text-align: center; }

.scss-list-item {
  margin-bottom: 30px; }
  .scss-list-item .border {
    margin-bottom: 8px;
    border: 8px solid #DBDBDB; }
  .scss-list-item .product-title {
    margin: 0;
    text-transform: uppercase; }
  .scss-list-item .display {
    display: flex; }
    .scss-list-item .display .grow {
      flex-grow: 1; }
  .scss-list-item .product-button {
    height: 30px;
    text-transform: uppercase;
    background: #EFB784;
    color: #fff;
    padding: 5px 12px;
    border: 0px; }

